.modal {
  /*position: absolute;*/
  position: fixed;
  overflow-x: hidden;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1050;
  display: none;
  background-color: rgba(0,0,0,.3);
  outline: none;
  color: #555;
  text-align: center;
  white-space: nowrap;
  -webkit-overflow-scrolling: touch;
}
.modal:before,
  .modal-dialog {
    display: inline-block;
    vertical-align: middle;
}
.modal:before {
    content: "";
    height: 100%;
    margin: -2px;
}
